/*--descisions--*/
.descisions{
	padding-top: rem(113px);

	.wrapper{
		h2{
			margin-bottom: rem(45px);
		}
		.descisions-item{
			@include col();
			@include col-4();
			@include col-lg-6();
			@include col-sm-12();
			margin-bottom: rem(107px);

			.descisions-item__icon{
				width: 64px;
				height: 64px;
				margin-bottom: 45px;
				position: relative;

				img{
					max-width: 100%;
				}

				&:after{
					content: '';
					position: absolute;
					display: block;
					width: 31px;
					height: 3px;
					bottom: -20px;
					left: 0;
					background-color: $accent;
				}
			}

			.descisions-item__title {
				margin-bottom: rem(18px);
				min-height: 45px;
				overflow: hidden;
				font-family: $m;

				color: $accent;
			}

			.descisions-item__text{
				margin-bottom: rem(40px);
					font-size: 14px;
					line-height: 20px;
				strong{
					font-family: $b;
				}
			}

			.button{
				width: 100%;
			}
		}
	}
}

@media only screen and (max-width : 780px){
	.descisions .wrapper .descisions-item .descisions-item__title{
		font-size: 24px;
	}
	.descisions .wrapper .descisions-item .button{
		width: 362px;
	}
}
@media only screen and (max-width : 480px){
	.descisions .wrapper .descisions-item .descisions-item__title{
		font-size: 18px;
	}
	.descisions .wrapper .descisions-item .button{
		width: 100%;
	}

}
/*--end descisions--*/