/*--consult--*/
.consult{
	position: relative;
	padding-top: rem(79px);
	padding-bottom: rem(59px);
	z-index: 2;

	.red-decor{
		right: -300px;
		left: auto;
		height: calc(100% + 184px);
		bottom: -45px;
		top:auto;
		width: calc((100% - 1200px)/2 + 200px);
		transform: skewX(-30deg);

		&:before{
			display: none;
		}
	}

	.wrapper{
		position: relative;
		.consult-img{
			position: absolute;
			bottom: -350px;
			left: -400px;
		}

		.title-wrapper{
			margin-bottom: rem(32px);
		}
		h2{
			margin-bottom: rem(20px);
		}

		.delf-subtitle{
			font-family: $r;
		}

		.delf-form{
			width: 100%;
			display: -webkit-flex;
			display: -moz-flex;
			display: -ms-flex;
			display: -o-flex;
			display: flex;
			align-items: center;
			flex-wrap:wrap;

			.delf-form-label{
				width: calc(50% - 28px);
				margin-left: 14px;
				margin-right: 14px;
				margin-bottom: 23px;

				.delf-label-name{
					color: #fff;
				}
			}
			.delf-form-consent{
				color: #fff;

				a{
					color: #fff;
				}
			}
			.delf-form-button{
				width: 558px;
				margin-top: 35px;
				box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.25);
			}
			.delf-form__bottom{
				width: 100%;
			}
		}
	}

}

@media only screen and (max-width : 1200px){
	
	.consult .wrapper .delf-form .delf-form-button{
		width: 100%;
	}
}
@media only screen and (max-width : 780px){
	.consult .wrapper .delf-form .delf-form-label{
		width: 100%;
		margin-right: 0;
		margin-left: 0;

	}
}
@media only screen and (max-width : 480px){
	.consult .wrapper .delf-form{
		padding-right: 15px;
		padding-left: 15px;
	}
}
/*--end consult--*/