/**--delf-filter--*/
.delf-filter{
		margin-left: -8px;
		margin-right: -8px;
		margin: 0 auto;
		margin-top: 34px;
		margin-bottom: 35px;
	
	.delf-filter__item{
		position: relative;
		width: calc(25% - 8px);
		// outline: 1px solid;
		margin-right: 4px;
		margin-left: 4px;
	}
	.delf-filter__name{
		position: absolute;
		left: 0;
		bottom: 40px;
		padding-left: 15px;
		margin-bottom: 10px;
		font-size: 14px;
	}
	.dropdown{
		display: block;
	}
	.dropdown-toggle{
		position: relative;
			display: block;
			box-shadow: inset 0px 1px 2px rgba(0, 0, 0, 0.25);
			background-color: #fff;
			.dropdown-text{
				display: block;
				color: #000;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
		}
		.dropdown-icon{
			position: absolute;
			width: 17px;
			height: 10px;
			background:  url(../img/icons-svg/chevron-down-black.svg) 0 0 no-repeat;
			background-size: 100%;
			right: 12px;
			top: 50%;
			transform: translateY(-50%);

			&:after{
				display: none;
			}
		}
	}
	.dropdown-selected .dropdown-link{
		background-color: $accent;
	}
}

@media only screen and (max-width : 980px){
	.delf-filter{
		margin-top: 0;
		margin-bottom: 0;
	}
	.delf-filter .delf-filter__item{
		width: calc(50% - 8px);
		margin-bottom: 30px;
	}
	.delf-filter .delf-filter__name{
		position: static;
		br{
			display: none;
		}
	}
}
@media only screen and (max-width : 780px){
	.delf-filter .delf-filter__item{
		width: 100%;

	}
}
@media only screen and (max-width : 480px){
	.wrapper.delf-filter{
		margin-left: 0;
		margin-right: 0;
		padding-right: 15px;
		padding-left: 15px;
	}
}
/**--end delf-filter--*/