/*--header--*/
.header{
	position: relative;
	z-index: 10;

	&-mini{
		.wrapper{
			position: relative;
			z-index: 1;
		}

	}
	.header-top{
		position: relative;
		padding-top: 21px;
		padding-bottom: 14px;
		background-color: #F2F2F2;

		.red-decor{

			width: calc((100% - 1200px)/2 + 150px);

			&:before{
				left: -50px;
				width: 100px;

			}

		}

		.header-menu{
			display: none;
		}
		.wrapper{
			position: relative;
			z-index: 1;
			justify-content: space-between;
			align-items: center;

			.header-call{
				// margin-right: 30px;
				width: auto;
				padding: 0;
				background-color: transparent;
				box-shadow: none;
				border: none;
				color: #fff;
				font-family: $m;
				text-transform: none;

				&:hover{
					color: #75CFFF;
				} 
				&:active{
					color: #000;
				}
				

			}

			.header-left,
			.header-right{
				@include col();
			}
			// .header-left,
			.header-right{
				flex: 1 1 auto;
				margin-left: rem(52px);
				display: -webkit-flex;
				display: -moz-flex;
				display: -ms-flex;
				display: -o-flex;
				display: flex;
				justify-content: space-between;
				align-items: center;
			}

			.header-logo{
				display: -webkit-flex;
				display: -moz-flex;
				display: -ms-flex;
				display: -o-flex;
				display: flex;
				align-items: center;
				p{
					max-width: 318px;
					font-size: rem(12px);
					font-family: $m;
					line-height: rem(16px);
					margin: 0;
					margin-left: rem(52px);
				}
			}

			.header-coordinates{
				display: -webkit-flex;
				display: -moz-flex;
				display: -ms-flex;
				display: -o-flex;
				display: flex;
				align-items: center;
			}

			.header-tel{
				text-decoration: none;
				.icon{
					display: inline-block;
					vertical-align: middle;
					margin-right: 16px;
					font-size: 30px;
					color: $accent;
				}
				span{
					color: $font;
					font-family: $m;
				}
			}

			.header-loc{
				margin-left: 35px;
				.dropdown{
					
					margin-top: -5px;
					}
				.dropdown-toggle{
					padding: 0;
					background-color: transparent;
					box-shadow: none;
					// max-width: 100px;

				}
				.dropdown-icon{
					position: absolute;
					width: 17px;
					height: 10px;
					background:  url(../img/icons-svg/chevron-down.svg) 0 0 no-repeat;
					background-size: 100%;
					opacity: 1;
					top: 50%;
					transform: translateY(-50%);
					right: -25px;
					color: $font;

					&:after{
						display: none;
					}
				}
				.dropdown-toggle .dropdown-text{
					max-width: 100px;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
				
					font-size: 18px;
					font-family: $m;
					color: $font;
					
				}
				.dropdown-selected .dropdown-link{
					background: $accent;
				}
				.dropdown-selected .dropdown-link{
					color: $font;
				}
			}


		}
	}
	.header-bottom{
		position: relative;
		padding: 16px 0 21px 0;
		box-shadow: 0px 10px 20px rgba(70, 136, 158, 0.2);
		

		.button{
			display: none;
			width: 100%;

		}
	}

	.header-menu{
			display: -webkit-flex;
			display: -moz-flex;
			display: -ms-flex;
			display: -o-flex;
			display: flex;
			align-items: flex-start;
			@include col();

			li{
				margin-left: 78px;
				list-style-type: none;
				a{
					position: relative;
					color: #000;
					text-decoration: none;


					&:after{
						content: '';
						position: absolute;
						left: 0;
						bottom: -7px;
						display: none;
						width: calc(100% + 32px);
						height: 1px;
						background-color: $accent;

					}

					&:hover, &:active{
						&:after{
							display: block;
						}
					}
					
				}
				&:first-child{
					margin-left: 0;
				}
				&.active{
					a{
						&:after{
							display: block;
						}

					}
				}
			}
		}

	&--fixed{
		position: fixed;
		width: 100%;
		top: 0;
		left: 0;
		z-index: 100;
		box-shadow: 0px 10px 20px rgba(70, 136, 158, 0.2);

		.header-top{
			font-size: 13px;


			.header-menu{
				display: -webkit-flex;
				display: -moz-flex;
				display: -ms-flex;
				display: -o-flex;
				display: flex;
				margin-left: 30px;
				li{
					margin-left: 20px;

					a{
						&:after{
							width: 100%;
						}												
					}

					&:first-child{
						margin-left: 0;
					}
				}
			}
			.wrapper{



				.header-coordinates{
					display: block;
					margin-left: auto;
					margin-right: auto;
					text-align: center;
				}

				.header-loc{
						margin-left: 0;
						.dropdown{
							margin-left: -10px;
							text-align: center;
						}
					 .dropdown-toggle .dropdown-text{
						font-size: 14px;
					}
					
				}
			}
		}
		.header-top .wrapper .header-logo{
			img{
				max-width: 76px !important;
			}
		}
		.header-top .wrapper .header-logo p{
			display: none;
		}
		.header-bottom{
			padding: 0;
			
			.header-menu{
				display: none;
			}
		
		}
		

	}
}

@media only screen and (max-width : 1200px){
	.header .header-top .wrapper .header-call{
		color: $accent;
	}
	.header .header-top .wrapper .header-loc .dropdown-toggle .dropdown-text{
		max-width: 150px;
	}
	.header .header-top .wrapper .header-loc{
		margin-right: 15px;
	}
	.header{
		.header-top{
			font-size: 14px;

			.header-logo{
				img{
					max-width: 100px;
				}
			}

			.button{
				padding: 8px 15px;
			}
		}
	}
	.header .header-top .wrapper .button{
				margin-right: 0;
		
	}
	.header .header-top .wrapper .header-loc .dropdown-toggle .dropdown-text {
		font-size: 14px;
	}
	.header .header-top .wrapper .header-logo p{
		max-width: 200px;
	}
	.header .header-top .wrapper .header-loc .dropdown{
		width: 150px;
		margin-top: -4px;
	}
	.header .header-top .wrapper .header-right{
		margin-left: 48px;
		// justify-content: flex-end;
	}
	.header .header-top .wrapper .header-loc{
		margin-left: 30px;
		
	}
	.header--fixed .header-top .wrapper .header-loc{
		margin-left: 0;
		
	}
	.header .header-top .wrapper .header-coordinates{
		margin-left: auto;

	}
	.header .header-top .wrapper .header-loc .dropdown{
		margin-left: 15px;
	}
	.header .header-top .wrapper .header-loc .dropdown{
		margin-left: -5px;
	}
	.header--fixed{
		.header-top{
			.header-menu{
				margin-left: 25px;
			}
			.wrapper{
				.header-right{
					margin-left: 15px;
				}
				
			}
		}
	}


}
@media only screen and (max-width : 1100px){
	.header--fixed .header-top .wrapper .header-coordinates{
		display: block;
	}
	.header--fixed .header-top .wrapper .header-tel{
		font-size: 12px;

		.icon{
			margin-right: 8px;
			font-size: 24px;
		}
	}
	.header--fixed .header-menu li a{
		font-size: 12px;
	}
	
	.header--fixed .header-top .header-menu{
		margin-left: 15px;
	}

}
/* Medium Devices, Desktops */
@media only screen and (max-width : 960px){
	.header--fixed .header-top{
		.button{
			display: none;
		}
	}
	.header .header-menu li{
		margin-left: 50px;
	}

	.header .header-top .wrapper .header-logo p{
		max-width: 180px;
		margin-left: 10px;
		font-size: 12px;
		line-height: normal;

	}
	.header .header-top .wrapper .header-tel .icon {
		font-size: 20px;
		margin-right: 8px;
	}
	.header .header-top .wrapper .header-right{
		margin-left: 20px;
	}
	.header--fixed .header-top .wrapper .header-right{
		margin-left: 0;
		margin-right: 0;
	}
	.header--fixed .header-top .header-menu {
		margin-left: 10px;
	}
}
	
	
/* Small Devices, Tablets */
@media only screen and (max-width : 840px){
	.header .header-top .wrapper .header-logo p{
		display: none;
	}


}
@media only screen and (max-width : 780px){
	.header .header-top{
		z-index: 3;
	}
	.header .header-top .wrapper .header-call{
		display: none;
	}
	
	.header .header-top .wrapper .header-logo p{
		display: none;
	}
	.header .header-top{
		padding: 10px 0;
	}
	.header--fixed .header-top{
		padding: 10px 0;

		.header-menu{
			display: none;
		}
	}
	.header .header-bottom{
		position: relative;


	}
	.header .header-bottom .header-menu{
		display: none;
		position: absolute;
		padding-bottom: 50px;
		top: 100%;
		left: 0;
		width: 100%;
		background-color: #fff;
		z-index: 99;
		height: calc(100vh - 100px);
		overflow: auto;
		margin-left: 0;
		margin-right: 0;

		li{
			margin-left: 0;
			border-bottom: 1px solid $accent;

			a{
				display: block;
				padding: 10px 15px;
				font-size: 24px;

				&:hover, &:active{
					color: $accent;
					&:after{
						display: none;
					}
				}
				
			}

			&.active{
				a{
					text-decoration: none;
					color: $accent;

					&:after{
						display: none;
					}
				}
			}
			&:last-child{
				border-bottom: none;
			}
		}

		&.opened{
			display: block;
		}

	}
	.header .header-bottom{
		padding: 5px 0;
		position: absolute;
		top: 100%;
		width: 100%;
		z-index: 2;
		box-shadow: none;
	}
	
	.header--fixed .header-bottom{
		padding: 0;

	}
	.header--fixed .header-top .wrapper .button{
		margin-left: 0;
	}
	.header .header-top .button{
		display: none;
	}
	
	.header .header-top .toggle-mnu{
		display: block;
		// margin-left: auto;
		position: absolute;
		margin-top: 0;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
	}
	.header .header-bottom .button{
		display: block;
	}

	.header .header-top .wrapper .header-right{
		justify-content: center;
		position: relative;
	}
	.header--fixed{
		.header-bottom{
			.button{
				display: none;
			}
			.header-menu{
				height: calc(100vh - 59px);
			}
		}
		.header-left{
			display: none;
		}
		.header-top{
			.button{
				display: block;
				margin-right: auto;
				margin-left: auto;
				
			}
			.wrapper{
				.header-right{
					margin-left: 0;
					justify-content: space-between;
					
				}
			}
		}
	}
	.header .header-top .wrapper .header-coordinates{
		margin-left: 0;
		margin-right: 0;
	}
	.header--fixed .header-top .toggle-mnu{
		position: static;
		transform: translateY(0);
	}
	.header--fixed .header-top .wrapper .button{
		margin-left: auto;
		margin-right: auto;
	}
	.header--fixed .header-top .wrapper .header-call{
		display: block;
	}
