}

@media only screen and (max-width : 560px){
	.header .header-top .wrapper .header-coordinates{
		display: block;
	}
	.header .header-top .wrapper .header-loc{
		margin-left: 0;
	}
	.header .header-top .wrapper .header-loc .dropdown{
		margin-left: 0;
		width: 100%;
		text-align: center;

	}
	.header .header-top .wrapper .header-loc .dropdown-toggle .dropdown-text{
		font-size: 12px;
	}
	
	.header .header-top .wrapper .header-right{
		margin-left: 0;
	}
	.header .header-top .wrapper .header-coordinates{
		position: relative;
		left: -20px;
	}
	.header--fixed .header-top .wrapper .header-coordinates{
		left: 0;
	}

}
	

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px){
	.header .header-top .header-logo img {
		max-width: 76px;
	}
	.header--fixed{
		.header-top{
			.button{
				width: auto;
				margin-left: 0;
				margin-right: 20%;
				font-size: 10px;
			}
			.header-coordinates{
				font-size: 12px;
			}
			.wrapper .header-loc .dropdown-toggle .dropdown-text{
				font-size: 12px;
			}
			.wrapper .header-loc .dropdown{
				width: 120px;
			}
			// .wrapper{
			// 	.header-right{
			// 		justify-content: space-around;
			// 	}
			// }
			
		}

	}
	.header .header-bottom{
		padding-left: 15px;
		padding-right: 15px;
	}
	.header--fixed .header-top .wrapper .header-coordinates{
		margin-left: 15px;
	}
	.header--fixed .header-top .wrapper .header-right{
		margin-right: 15px;
	}
}

@media only screen and (max-width : 375px){
	.header--fixed{
		.header-top{
			.button{
				margin-right: 15%;
			}
		}
	}
}

@media only screen and (max-width : 320px){

	.header .header-top .wrapper .header-right{
		justify-content: flex-start;
	}
	.header--fixed .header-top .wrapper .header-right{
		justify-content: space-between;
	}
	.header .header-top .wrapper .header-coordinates{
		margin-left: 20px;
	}
	
}	

@media only screen and (min-width : 781px){
	.header .header-bottom .header-menu{
		display: flex !important;
	}
	.header--fixed .header-bottom .header-menu{
		display: none !important;
	}
	html{

		&.freezed{
			overflow: visible;
			width: auto;
			height: auto;

			body{
				overflow-y: visible;
		    width: auto;
		  }
		}
	}
}
