/*--card-chars--*/
.card-chars{
	padding-top: 177px;
	background-color: #FAFAFA;
	// padding-bottom: 63px;

	.wrapper{
		justify-content: center;
		background-color: #fff;
		padding-left: 0;
		padding-right: 0;
		
		.card-chars__tabs{
			// @extend .delf-padded-left;
			// @include col();
			// @include col-12();
			// @include col-md-12();
			width: 100%;
			margin-left: -15px;
			margin-right: -15px;

			.tabs__list{
				display: -webkit-flex;
				display: -moz-flex;
				display: -ms-flex;
				display: -o-flex;
				display: flex;
				align-items: flex-start;
				margin-bottom: rem(77px);
				.card-chars__tabs-item{

					flex: 1 1 auto;
					list-style-type: none;
						background-color: #FAFAFA;

					.card-chars__tabs-link{
						display: block;
						padding: 20px 0 30px 0;
						text-align: center;
					}

					&.tabs__item--active{
						background-color: #fff;
						.card-chars__tabs-link{
						text-decoration: none;
							color: $accent;
						}
					}

					a{
						color: #979797;
						font-family: $m;

						&:hover, &:active{
							color: $accent;
							text-decoration: none;
						}
						
					}

					&:first-child{
						border-left-color: $accent;
					}
				}
			}

			.tabs__content{
				padding-left: 68px;
				padding-right: 68px;
				line-height: 21px;

				.card-chars__content{
					text-align: left;
				}

				.tabs__item{
					display: none;
					list-style-type: none;
					text-align: center;
					// border: 1px solid $accent;
					// border-bottom-color: transparent;

					.cards-chars__toggle{
						// margin-bottom: 15px;
						padding: 20px 0;
						display: none;
						color: #979797;
						font-family: $l;
							text-decoration: none;
						

						&:hover,
						&.active{
							color: $accent;
						}
					}

					&.active{
						display: block;
						border-bottom-color: transparent;
					}
					// &:last-child{
					// 	border-bottom-color: $accent;
					// }

				}
				p{
					line-height: 21px;

				}

				.card-chars__block{
					margin-bottom: rem(50px);

					table{
						width: 100%;
						font-size: 14px;
						h3{
							margin: 10px 0;
						}
						tr, th{
							&:nth-child(odd){
								background-color: #FAFAFA;
							}
						}
						td, th{
							padding: 15px 10px;

						}
					}

					h3{
						text-align: left;
					}

					li{
						list-style-type: none;
						text-align: left;
						font-family: $l;
					}

					p{
						text-align: left;
						line-height: 28px;
						font-family: $l;
					}
				}

				// .ipc-list-marked-dash{
				// 	margin-bottom: 30px;
				// }
			}
		}
	}

}

@media only screen and (max-width : 1100px){
	.card-chars .wrapper .card-chars__tabs .tabs__list .card-chars__tabs-item .card-chars__tabs-link{
		font-size: 13px;
	}
}

@media only screen and (max-width : 780px){
	.card-chars .wrapper .card-chars__tabs .tabs__content .tabs__item{
		padding-left: 20px;
		padding-right: 20px;
		border: 1px solid $accent;
    border-bottom-color: transparent;
	}
	.card-chars .wrapper .card-chars__tabs .tabs__content .tabs__item:last-child {
    border-bottom-color: $accent;
}
	.card-chars{
		padding-top: 50px;
		padding-bottom: 30px;
	}
 .card-chars__content{
		display: none;
	}
	.card-chars .wrapper .card-chars__tabs .tabs__list{
		display: none;
	}
	.card-chars .wrapper .card-chars__tabs .tabs__content{
		.tabs__item{
			display: block;
			.card-chars__content{
				display: none;
			}

			&.active{
				.card-chars__content{
					display: block;
				}
			}
		}
		
	}
	.card-chars .wrapper .card-chars__tabs .tabs__content .tabs__item .cards-chars__toggle{
		display: block;
	}
	.card-chars .wrapper .card-chars__tabs .tabs__content .card-chars__block{
		font-size: 14px;
	}
	.card-chars .wrapper .card-chars__tabs .tabs__content .card-chars__block{
		.ipc-list-marked-dash li{
			line-height: 24px;
		}
	}
	.card-chars .wrapper .card-chars__tabs .tabs__content .card-chars__block p{
		line-height: 24px;
	}
	.card-chars .wrapper .card-chars__tabs{
		margin-left: 0;
		margin-right: 0;
		width: 100%;
	}
	.card-chars .wrapper{
		padding-left: 15px;
		padding-right: 15px;
		background-color: #FAFAFA;
	}
	.card-chars .wrapper .card-chars__tabs .tabs__content{
		padding-left: 0;
		padding-right: 0;
		margin-left: 15px;
		margin-right: 15px;
		background-color: #fff;
	}
}
@media only screen and (max-width : 480px){
	.card-chars .wrapper{
		padding-left: 0;
		padding-right: 0;

	}
	.card-chars .wrapper .card-chars__tabs .tabs__content .card-chars__block h3{
		font-size: 16px;
	}
	.card-chars .wrapper .card-chars__tabs .tabs__content .tabs__item{
		padding-left: 10px;
		padding-right: 10px;
	}
	.card-chars .wrapper .card-chars__tabs .tabs__content .card-chars__block table{
		font-size: 12px;
	}
	.card-chars .wrapper .card-chars__tabs .tabs__content .card-chars__block table h3{
		margin: 0;
		font-size: 14px;
	}
	.card-top .wrapper .card-top__buttons .button{
		padding-top: 20px;
		padding-bottom: 20px;
		font-size: 13px;
	}
}
/*--end card-chars--*/