/*--card-top--*/
.card-top{
	padding-top: rem(100px);
	background-color: #F2F2F2;
	
	.wrapper{

		.card-view,
		.card-top__info{
			@include col();
			@include col-6();
			@include col-sm-12();
		}
		// .card-view{
		// 	@include col-md-5();
		// }
		// .card-top__info{
		// 	@include col-md-7();
		// }

		.card-view{
			.card-display{

					margin-bottom: rem(20px);
				// .slick-list{
				// 	overflow: visible;
				// }

				// .slick-slide{
				// 	visibility: hidden;
				// 	// &.slick-active{
				// 	// 	visibility: visible;
				// 	// }
				// }


			}
			.card-display__wrap{
				padding: 5px 10px 20px 10px;
			}

			.card-display__slide{
				height: 449px;
				display: block;
				position: relative;
				// text-align: center;
				background-color: #fff;
				box-shadow: 0px 10px 20px rgba(70, 136, 158, 0.2);
				overflow: hidden;

				.card-display__sale{
					position: absolute;
					left: -91px;
					top: -91px;
					z-index: 1;
					width: 180px;
					height: 180px;
					background-color: $accent;
					color: #fff;
					font-size: 24px;
					line-height: 299px;
					text-align: center;
					font-family: $b;
					transform: rotate(-45deg);

				}

				img{
				    display: block;
				    max-width: 100%;
				    max-height: 100%;
				    position: absolute;
				    top: 50%;
				    left: 50%;
				    transform: translate(-50%,-50%);
					
					// max-width: 100%;
					// display: inline-block;

				}


			}
			.card-view__bottom{
				padding: 0 30px;
			}

			.card-display-nav{
				&__inner{
						width: 88px;
						height: 82px;
						display: inline-block;
						position: relative;
						overflow: hidden;
						background-color: #fff;
						border: 1px solid transparent;
						cursor: pointer;
						overflow: hidden;

						img{
							display: block;
							max-width: 100%;
							max-height: 100%;
							position: absolute;
							top: 50%;
							left: 50%;
							transform: translate(-50%,-50%);
						}
						&:hover, &:active{
							border-color: $accent;
							box-shadow: 0px 10px 20px rgba(103, 212, 61, 0.2);
						}
						
					}
					.card-display-nav__play{
						position: absolute;
						width: 37px;
						height: 37px;
						top: 50%;
						left: 50%;
						transform: translate(-50% -50%);
						z-index: 2;
					}

				.slick-list{
					padding-bottom: 20px !important;
				}
				.slick-prev,
				.slick-next{
					height: 50px;
					&:before{
						display: block;
						height: 25px;
						width: 25px;
						position: absolute;
						top: 50%;
						margin-top: -100%;
						content: '';
					}
					&:hover, &:active{
						&:before{
						border-color: $accent;
							
						}
					}
				}
				.slick-prev{
					&:before{
						border-top: 4px solid  #C4C4C4;
						border-left: 4px solid  #C4C4C4;
						transform: rotate(-45deg);
					}
				}
				.slick-next{
					// right: -15px;
					&:before{
					left: -15px;
						border-top: 4px solid  #C4C4C4;
						border-right: 4px solid  #C4C4C4;
						transform: rotate(45deg);
					}

					
					
				}

				.slick-slide{
					text-align: center;
					&.slick-center{
						.card-display-nav__inner{
						box-shadow: 0px 10px 20px rgba(103, 212, 61, 0.2);
							
						}
					}
				}
				.card-display-nav__slide{
					text-align: center;
				}
			}
		}
		.card-top__info{
			.card-top__title{
				font-size: rem(36px);
				font-family: $b;
				margin-bottom: rem(15px);
			}
			p{
				margin-top: 0;
				margin-bottom: 25px;
				font-family: $l;
			}
		}
		.card-top__delivery{
			display: -webkit-flex;
			display: -moz-flex;
			display: -ms-flex;
			display: -o-flex;
			display: flex;
			align-items: flex-start;
			margin-bottom: 44px;
			
			.card-top__onhold{
				margin-right: 70px;
			}
			.card-top__onhold,
			.card-top__del-date{
				.delf-icon{
					display: inline-block;
					margin-right: 10px;
					vertical-align: middle;
					box-shadow: 0px 10px 20px rgba(239, 60, 73, 0.2);
				}
				span{
					display: inline-block;
					vertical-align: middle;
					font-family: $m;
				}
			}
		}

		.card-top__chars{
			margin-bottom: 35px;
			.card-top__chars-title{
				margin-bottom: 18px;
				font-family: $m;
			}

			.delf-card__info{
				font-size: 18px;

				tr td:first-child{
					width: 282px;
				}
			  tr td:last-child{
					padding-left: 10px;
				}
				tr td{
					padding-bottom: 20px;
				}
				.ipc-card__key{
					font-family: $l
				}
				.ipc-card__value{
					font-family: $m
				}
			}
		}

		.card-top__final{
			display: -webkit-flex;
			display: -moz-flex;
			display: -ms-flex;
			display: -o-flex;
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
			// margin-bottom: 37px;

			.card-top__price,
			.card-top__request{
				width: 49%;

			}

			
			.card-top__request{
				display: -webkit-flex;
				display: -moz-flex;
				display: -ms-flex;
				display: -o-flex;
				display: flex;
				align-items: center;
				text-align: center;
				.button{
					margin-bottom: 23px;
				}

			}

			.card-top__order-request{
				color: #000;
				font-family: $l;
				font-size: 16px;

				&:hover, &:active{
					text-decoration: none;
				}
				
			}

			.card-top__price-val{
				margin-bottom: 21px;
				margin-top: 10px;
				font-size: rem(48px);
				font-family: $m;

				&--old{
					margin-bottom: 0;
					margin-top: 0;
					font-size: rem(28px);
					color: #8A8A8A;
					text-decoration: line-through;
				}
			}
		}

		.card-top__buttons{
			display: -webkit-flex;
			display: -moz-flex;
			display: -ms-flex;
			display: -o-flex;
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			margin-bottom: rem(30px);
			.button{
				width: 49%;
			}
		}
	
		.card-top__chars-button{
			margin-bottom: rem(50px);
		}
		.card-top__full-chars{
				font-size: 14px;
				line-height: 17px;
				font-family: $m;
				cursor: pointer;
				.icon{
					// display: inline-block;
					margin-left: 15px;
					font-size: 8px;
				}
				span{
					display: inline-block;
					padding-bottom: 3px;
					border-bottom: 2px dashed $accent;
				}

				&:hover, &:active{
					span{

					border-bottom-color: transparent;
					}
				}
				
			}


	}
		.card-top__features{
			justify-content: space-between;
			flex-wrap: wrap;
			padding: 56px 70px;
			bottom: -123px;
			margin-top: -123px;
			position: relative;
			background: #FFFFFF;
			box-shadow: 0px 10px 20px rgba(70, 136, 158, 0.2);

			.card-top__feature{
				.card-top__feature-icon{
					width: 64px;
					margin-bottom: 14px;
					img{
						max-width: 100%;
					}
				}
				p{
					margin: 0;
					font-family: $m;
					line-height: 26px;
					span{
						display: block;
					}
				}
			}
		}
}

@media only screen and (max-width : 1200px){
	.card-top .wrapper .card-view .card-display__slide{
		height: 360px;
	}
	.card-top .wrapper .card-top__buttons{
		.button{
			font-size: 12px;
		}
	}
}

@media only screen and (max-width : 1100px){
	.card-top .wrapper .card-top__buttons{
		.button{
			font-size: 10px;
		}
	}
	.card-top .wrapper .card-view{
			padding-left: 0;
	}
	.card-top .wrapper .card-top__info{
		.card-top__descr{
			font-size: 16px;
		}
	}
	.card-top .wrapper .card-top__delivery{
		justify-content: space-between;
		font-size: 14px;
	}
	.card-top .wrapper .card-top__delivery .card-top__onhold{
		margin-right: 0;
	}

}
@media only screen and (max-width : 980px){
	.card-top .wrapper .card-top__buttons{
		.button{
			font-size: 10px;
		}
	}
	.card-top .wrapper .card-top__chars .delf-card__info tr td:first-child{
		width: 50%;
	}
	// .card-top .wrapper .card-top__final{
	// 	display: block;
	// }
	// .card-top .wrapper .card-top__final .card-top__price,
	// .card-top .wrapper .card-top__final .card-top__request{
	// 	width: 100%;
	// }
	.card-top .wrapper .card-top__final .card-top__price{
		margin-bottom: 15px;
	}
	.card-top .card-top__features{
		padding-left: 30px;
		padding-right: 30px;
	}
	.card-top .card-top__features .card-top__feature p{
		font-size: 16px;
	}
	.card-top .wrapper .card-top__final .card-top__price,
	.card-top .wrapper .card-top__final .card-top__request{
		width: auto;
	}

}
@media only screen and (max-width : 940px){
	.card-top .wrapper .card-top__buttons{
		justify-content: flex-start;
		.button{
			width: auto;

			&:first-child{
				margin-right: 5px;
			}
		}
	}
}
@media only screen and (max-width : 780px){
	.card-top .wrapper .card-top__buttons{
		justify-content: space-between;
		.button{
			width: 49%;
			font-size: 12px;
			&:first-child{
				margin-right: 0;
			}
		}
	}
	.card-top__chars-button{
		text-align: center;
	}

	.card-top .wrapper .card-view .card-display__slide{
		height: 320px;
	}
	.card-top .wrapper .card-view{
		padding-left: 15%;
		padding-right:  15%;
	}
	.card-top .card-top__features{
		display: none;
	}

}

@media only screen and (max-width : 560px){
	.card-top .wrapper .card-view{
		padding-left: 10%;
		padding-right:  10%;
	}
	.card-top .wrapper .card-top__buttons{
		display: block;
		.button{
			width: 100%;
			font-size: 12px;

			&:first-child{
				margin-bottom: 20px;
			}
		}
	}
	.card-top .wrapper .card-view .card-display__slide .card-display__sale{
		width: 48px;
		height: 48px;
		font-size: 18px;
		line-height: 48px;
	}
}
@media only screen and (max-width : 480px){

	.card-top{
		padding-top: 86px;
	}
	.card-top .wrapper .card-view{
		padding-left: 0;
		padding-right: 0;
	}
	.card-top .wrapper .card-view .card-display-nav .slick-slide{
		// margin: 0 10px;
	}
	.card-top .wrapper .card-view .card-display-nav__inner{
		width: 78px;
		height: 76px;
	}
	.card-top .wrapper .card-top__delivery{
		display: block;
	}
	.card-top .wrapper .card-top__delivery .card-top__onhold{
		margin-bottom: 15px;
	}
	.card-top .wrapper .card-top__chars .ipc-card__info{
		font-size: 14px;
	}
	.card-top .wrapper .card-top__chars .delf-card__info{
		font-size: 14px;
	}
}
@media only screen and (max-width : 375px){
	.card-top .wrapper .card-view .card-display-nav__inner{
		width: 58px;
		height: 56px;
	}
	.card-top .wrapper .card-view .card-display__slide{
		height: 220px;
	}
	.card-top .wrapper .card-top__final{
		display: block;
		margin-bottom: 30px;
	}
}



/*--end card-top--*/