/*--in-gigits--*/
.in-digits{
		padding-top: rem(107px);
		position: relative;

	.in-digits__line{
		position: absolute;
		@include absolute-center;
		top: auto;
		width: 620px;
		height: 1px;
		background-color: #DADADA;
	}
	h2{
		margin-bottom: rem(37px);
	}
	.in-digits__items{
		justify-content: space-around;
		flex-wrap: wrap;

		.in-digits__item{
			margin-bottom: 56px;
			text-align: center;
		}

		.in-digits__icon{
			position: relative;
			display: inline-block !important;
			width: 196px;
			height: 196px;
			margin-bottom: rem(24px);
			background: center no-repeat;
			background-size: 100%;

			span{
				@include absolute-center;
				font-size: rem(36px);
				line-height: 196px;
				font-family: $m
			}
		}
		.in-digits__caption{
			line-height: 26px;
			font-family: $m;
		}

		
	}
}

@media only screen and (max-width : 1100px){
	.in-digits__item{
		@include col();
	}
	.in-digits .in-digits__items .in-digits__icon{
		width: 140px;
		height: 140px;
	}
	.in-digits .in-digits__items .in-digits__icon span{
		font-size: 24px;
		line-height: 140px;
	}
	.in-digits .in-digits__items .in-digits__bold{
		font-size: 18px;
	}
	.in-digits .in-digits__items .in-digits__light{
		font-size: 16px;
	}
}
@media only screen and (max-width : 960px){
	.in-digits .in-digits__items .in-digits__icon{
		width: 110px;
		height: 110px;
	}
	.in-digits .in-digits__items .in-digits__icon span{
		font-size: 18px;
		line-height: 110px;
	}
	.in-digits .in-digits__items .in-digits__bold{
		font-size: 16px;
	}
	.in-digits .in-digits__items .in-digits__light{
		font-size: 14px;
	}
}

@media only screen and (max-width : 780px){
	.in-digits{
		display: none;
	}
}
/*--end in-gigits--*/