/*--kviz--*/
.kviz{
	position: relative;
	padding-top: 49px;
	padding-bottom: rem(96px);
	background-color: #EEEEEE;
	z-index: 1;

	

	.red-decor{
		right: auto;
		left: -300px;
		height: calc(100% + 184px);
		top: -45px;
		width: calc((100% - 1200px)/2 + 200px);
		transform: skewX(-30deg);

		&:before{
			display: none;
		}

	}
	// .kviz-visual{
	// 	position: relative;
		
	// }
	.wrapper{
		position: relative;
		justify-content: center;
			.decor-img{
				position: absolute;
				top: -215px;
				right: -400px;
			}
		.kviz-form{
			@include col();
			@include col-10();
			@include col-xs-12();

			.slick-list{
				padding-bottom: 15px;
			}
			.delf-custom-check{

				.delf-form-consent{
					font-family: $r;
				}
			}

			.kviz-icon{
				display: inline-block;
				margin-bottom: rem(46px);
			}

			.kviz-slide{
				.title-wrapper{
					margin-bottom: rem(66px);
				}
				&--question{
					.title-wrapper{
					margin-bottom: rem(30px);
						
					}
				}
				.button{
					cursor: pointer;
				}
				.delf-subtitle{
					margin-top: rem(30px);
					font-family: $r;
					color: $accent;
				}

				.kviz-variants{
					width: 100%;
					margin-bottom: rem(33px);
					padding: rem(37px) 0;
					display: -webkit-flex;
					display: -moz-flex;
					display: -ms-flex;
					display: -o-flex;
					display: flex;
					justify-content: space-around;
					border: 1px solid #C4C4C4;
				}

				.kviz-inputs{
					display: -webkit-flex;
					display: -moz-flex;
					display: -ms-flex;
					display: -o-flex;
					display: flex;
					justify-content: space-between;
					flex-wrap: wrap;

					.delf-form-label{
						width: calc(50% - 15px);
					}
				}
				.kviz-inputs__bottom{
					margin-top: 21px;
					margin-bottom: 24px;
					width: 100%;
					text-align: center;
				}
			}
		}
	}
}

@media only screen and (max-width : 780px){
	.kviz .wrapper .kviz-form .kviz-slide .kviz-inputs .delf-form-label{
		width: 100%;
		margin-left: 0;
		margin-right: 0;
		margin-bottom: 20px;
	}
	.kviz .wrapper .kviz-form .kviz-slide .kviz-inputs__bottom{
		margin-top: 0;
	}
	.kviz .wrapper .kviz-form .kviz-slide .kviz-variants{
		display: block;
		padding-left: rem(30px);
		padding-right: rem(30px);
		
		.delf-custom-check{
			display: block;
			margin-bottom: 15px;
		}
	}

}

/*--end kviz--*/