/* Extra Small Devices, Phones */
@media only screen and (max-width : 560px){
	html{
		font-size: 12px;
	}

	.contacts .contacts-map{
		height: 260px;
	}

	.delf-form{
		label{
			.delf-label-name{
				font-size: 14px;
			}

		}
			.delf-custom-check{
				span, a{
					font-size: 14px;
				}
			}
	}
	
	
	.button{
		font-size: 14px;
	}
	.delf-card .delf-card__title{
		font-size: 18px;
	}

}

@media only screen and (max-width : 480px){
	html{
		font-size: 10px;
	}
	.wrapper{
		padding-left: 0;
		padding-right: 0;
	}
	.button{
		width: 100%;
	}
	// .wrapper{
	// 	padding-left: 0;
	// 	padding-right: 0;
	// }
	// .header .wrapper{
	// 	padding-left: 15px;
	// 	padding-right: 15px;
	// }
	.delf-card__info{
		font-size: 14px;
	}
	.button-wrapper{
		width: 100%;
		margin-left: 0;
		margin-right: 0;
	}
	.button{
		width: 100%;
		padding-top: 20px;
		padding-bottom: 20px;
	}
	.delf-subtitle{
		font-size: 20px;
		line-height: normal;
	}
}
		

