/*--footer--*/
.footer{
	padding-top: rem(69px);
	padding-bottom: rem(76px);
	background-color:  #2A3D47;
	font-size: 14px;
	color: #DADADA;
	.wrapper{
		justify-content: space-between;
		
		.footer-column{
			@include col();
			@include col-lg-6();
			@include col-xxs-12();
		}
		.footer-column--without{
			padding-top: 45px;
		}
		.footer-column--logo{
			p{
				max-width: 163px;
				margin-bottom: 28px;
				font-size: 12px;
			}
			.footer-menu{
				li{
					margin-bottom: 20px;
				}

				a{
					span{
						display: block;
					}


				}
			}
		}
		.footer-column--contacts{
			padding-left: 68px;
		}
		.footer-title{
			margin-bottom: 24px;
			font-family: $m;
		}
		.footer-menu{
			li{
				list-style-type: none;
				margin-bottom: 12px;
			}
			a{
				color: #DADADA;
				&:hover{
					color: #75CFFF;					
				}
				&:active{
					color: #FF5965;					
				}
				
			}
		}
		.footer-phones{
			margin-bottom: 40px;
			line-height: 20px;

			a{
				display: block;
				text-decoration: none;
				color: #DADADA;
			}
		}
		.footer-email{
			margin-bottom: 15px;
			div{
				margin-bottom: 25px;
			}
			a{
				color: #DADADA;

				&:hover{
						color: #75CFFF;					
					}
					&:active{
						color: #FF5965;					
					}
			}
		}
		.footer-socials{
			margin-bottom: 22px;
		}

		.footer-address{
			.footer-title{
				// margin-bottom: 18px;
				font-style: normal;
			}
			p{
				margin-bottom: 23px;
				margin-top: 0;
				max-width: 267px;
				font-style: normal;
			}

		}
		.footer-column--address{
			
			.footer-socials{
			margin-top: 65px;
			}
		}
	}
}

@media only screen and (max-width : 1100px){
	.footer{
		padding-bottom: 0;
	}
	.footer .wrapper .footer-column{
		margin-bottom: 50px;
	}
	.footer .wrapper .footer-column--contacts{
		padding-left: 0;
	}
}
@media only screen and (max-width : 480px){
	.footer .wrapper .footer-column{
		margin-bottom: 30px;

	}
	.footer .wrapper .footer-column--without{
		padding-top: 0;
		
	}
	.footer .wrapper .footer-address p,
	.footer .wrapper .footer-column--logo p{
		max-width: initial;
	}
	.footer .wrapper .footer-column--address .footer-socials{
		margin-top: 30px;
	}	
}
/*--end footer--*/