/*--cards-slider-wrapper--*/
.cards-slider-container{
			display: block;
		

	.cards-slider-wrapper{
				width: calc(100% + 60px);
				margin-left: -30px;
				overflow: hidden;
			.slider-handlers-container{
				@include col();
				@include col-12();
				text-align: center;

				.slider-handlers{
					display: inline-block;
					margin-top: 30px;
				}
			}

			.slider-handlers__dots{
				display: inline-block;
				vertical-align: middle;
					margin: 0 40px;

				.slick-dots{
					position: static;
					margin-top: -8px;
				}
				.slick-dots li{
					width: 50px;
					height: 4px;
					margin-left: 10px;
					margin-right: 10px;
				}
				.slick-dots li button{
					width: 50px;
					height: 4px;
					padding: 0;
				}
				.slick-dots li button:before{
					content: '';
					width: 50px;
					height: 4px;
					background-color: #C4C4C4;
					right: 0;
					margin: auto;
				}
				.slick-dots li.slick-active button:before{
					background-color: $accent;

				}
			}
			.cards-slider{
				.slick-list{
					// padding: 0 0 20px 0;
					// margin-left: 10px;
					// padding-right: 15px;
					// width: calc(100% - 30px);
					padding-left: 30px;
					padding-right: 30px;
					overflow: visible;

				}
				.slick-slide{
					margin: 0 4px;
					
					opacity: 0;
					&.slick-active{
						opacity: 1;
					}

				}
				
			}
		}
}

@media only screen and (max-width : 1200px){
	.cards-slider-container .cards-slider-wrapper{
		width: 100%;
		margin-left: 0;
		padding: 0 15px;
	}
	.cards-slider-container .cards-slider-wrapper .cards-slider .slick-list{
		margin-left: 0;
	}
	.cards-slider-container .cards-slider-wrapper .cards-slider .slick-list{
		padding-left: 0;
		padding-right: 0;
	}
}

@media only screen and (max-width : 780px){
	.cards-slider-container .cards-slider-wrapper .cards-slider .slick-list{
		padding-right: 0;
	}
	.cards-slider-container .cards-slider-wrapper .slider-handlers-container .slider-handlers{
		position: relative;

		.slider-handlers__prev, .slider-handlers__next{
			position: absolute;
			top: 50%;
			// margin-top: -20px;
			transform: translateY(-50%);
		}
		.slider-handlers__next{
			left: auto;
			right: 0;
		}
	}
	.cards-slider-container .cards-slider-wrapper .slider-handlers__dots{
		padding-left: 30px;
		padding-right: 30px;
	}
	.cards-slider-container .cards-slider-wrapper .slider-handlers__dots .slick-dots li{
		margin-bottom: 7px;
		margin-right: 7px;
		margin-left: 7px;
	}
	.cards-slider-container .cards-slider-wrapper .slider-handlers-container {
		margin-bottom: 15px;
	}
}
@media only screen and (max-width : 560px){
	.cards-slider-container{
		padding: 0;
	}
	.cards-slider-container .cards-slider-wrapper .slider-handlers__dots .slick-dots li{
		margin-left: 2px;
		margin-right: 2px;
	}
	.slider-handlers__prev .icon, .slider-handlers__next .icon{
		font-size: 24px;
	}
	.cards-slider-container .cards-slider-wrapper .slider-handlers__dots{
		margin-left: 0;
		margin-right: 0;
	}
	.cards-slider-container .cards-slider-wrapper .slider-handlers-container .slider-handlers{
		position: relative;

	}
	.cards-slider-container .cards-slider-wrapper .slider-handlers-container {
		margin-bottom: 0;
	}
}

/*--end cards-slider-wrapper--*/