/*--delf-order--*/

.delf-order{
	position: relative;
	padding-top: rem(71px);
	padding-bottom: rem(84px);

	.red-decor{
		right: auto;
		left: -300px;
		height: calc(100% + 184px);
		top: -45px;
		width: calc((100% - 1200px)/2 + 200px);
		transform: skewX(-30deg);

		&:before{
			display: none;
		}

	}
	.wrapper{
		.title-wrapper{
			margin-bottom: rem(44px);
		}
		h2{
			margin-bottom: rem(30px);
		}

		.delf-subtitle{
			color: $accent;
		}

		.delf-form{
			display: -webkit-flex;
			display: -moz-flex;
			display: -ms-flex;
			display: -o-flex;
			display: flex;
			align-items: center;
			flex-wrap:wrap;

			.delf-form-label,
			.delf-form-button{
				width: calc(33.333333333% - 28px);
				margin-left: 14px;
				margin-right: 14px;
				margin-bottom: 23px;

				.delf-label-name{
					color: #fff;
				}
			}
			.delf-form-consent{
				color: #fff;

				a{
					color: #fff;
				}
			}
			.delf-form-button{
				margin-top: 35px;
				box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.25);
			}
			.delf-form__bottom{
				width: 100%;
			}
		}
	}
}

@media only screen and (max-width : 780px){
	.delf-order .wrapper .delf-form {
		justify-content: center;
	}
	.delf-order .wrapper .delf-form .delf-form-label{
		width: 100%;
		margin-left: 0;
		margin-right: 0;
	}
	.delf-order .wrapper .delf-form .delf-form-button{
		width: 362px;
		margin: 0;
		margin-bottom: 30px;
	}
}
@media only screen and (max-width : 480px){
	.delf-order .wrapper .delf-form {
		padding-left: 15px;
		padding-right: 15px;
	}
	.delf-order .wrapper .delf-form .delf-form-button{
		width: 100%;
	}
}
/*--end delf-order--*/