/*--.questions--*/
.questions{
	position: relative;
	background: #EEEEEE;
	z-index: 1;
	
		.red-decor{
		right: auto;
		left: -500px;
		height: calc(100% + 45px);
		top: -44px;
		width: calc((100% - 1200px)/2 + 780px);
		transform: skewX(-30deg);

		&:before{
			display: none;
		}
	}
	

	.wrapper{
		.questions-pic,
		.questions-form{
			@include col();
			@include col-6();
		}

		.questions-pic{
			position: relative;
			@include col-md-5();
			img{
				position: absolute;
				left: -60%;
				bottom: 0;
				max-width: 220%;
			}
		}
		
		.questions-form{
			padding-top: rem(69px);
			@include col-md-7();
			@include col-sm-12();
			position: relative;

			h2{
				margin-bottom: rem(35px);
				text-align: left;
			}

			.delf-form{

				.delf-form-label{
					margin-bottom: 24px;

					.delf-label-name{
						font-family: $r;
					}
				}
				.delf-custom-check{
					display: block;
					margin-bottom: 25px;
					font-family: $m;
					a{
						color: #000;
					}
					.delf-custom-box{
						margin-right: 17px;
						border-radius: 0;	
					}
				}

				button{
					width: 100%;
					margin-bottom: rem(87px);
					cursor: pointer;
				}
			}

			
		}
	}
}


@media only screen and (max-width : 1200px){
	.questions .wrapper .questions-pic img{
					// left: -40%;
					max-width: 200%;
				}
}
@media only screen and (max-width : 980px){

	.questions .wrapper .questions-pic img{
					left: -60%;
					max-width: 200%;
				}
		.questions .wrapper .questions-form .delf-form label input{
				padding: 12px 26px;
		}
		.questions .wrapper .questions-form .delf-form label .delf-label-name{
			margin-bottom: 5px;
		}
		.questions .wrapper .questions-form .delf-form button{
			margin-bottom: 30px;
		}
}
		
@media only screen and (max-width : 780px){
	.questions .wrapper .questions-pic{
		display: none;
	}
	.questions .wrapper .questions-form{
		padding-left: 10%;
		padding-right: 10%;
	}
	.questions .wrapper .questions-form h2{
		div{
			display: inline;
		}	
	}
}
@media only screen and (max-width : 560px){
	.questions .wrapper .questions-form{
		padding-left: 0;
		padding-right: 0;
	}
	.questions .wrapper .questions-form .delf-form label{
		margin-bottom: 15px;
	}
}
@media only screen and (max-width : 480px){
	.questions .wrapper .questions-form .delf-form button{
		padding-top: 20px;
		padding-bottom: 20px;
	}
	.questions .wrapper .questions-form {

		width: 100%;
	}
}

/*--end .questions--*/