/*--contacts--*/
.contacts{
	position: relative;

	
	.red-decor{
		right: auto;
		left: -300px;
		height: calc(100% - 56px);
		bottom: 0;
		top: auto;
		width: calc(50% + 350px);
		transform: skewX(-30deg);
		background-color: #EEEEEE;
		z-index: 1;

		&:before{
			display: none;
		}

	}
	.contacts-decor-wht{
		right: auto;
		left: -300px;
		height:  56px;
		top: 0;
		bottom: auto;
		// width: calc((100% - 1200px)/2 + 600px);
		width: calc(50% + 471px);
		transform: skewX(-30deg);
		background-color: #FAFAFA;
		// z-index: 1;

		&:before{
			display: none;
		}
	}

	.wrapper{
		// @include absolute-center;
		position: absolute;
		top: 0;
		width: 100%;
		left: 0;
		right: 0;
		// align-items: flex-start;
		// width: 100%;
		// height: 100%;
		z-index: 2;
		@include lg(justify-content, center);

		.title-wrapper{
			@include col();
			@include col-7();
			@include col-lg-11();
			@include col-sm-12();
		}
		h2{
			margin-top: rem(150px);
			margin-bottom: rem(65px);
		}

		.contacts-info{
			@include col();
			@include col-7();
			@include col-lg-11();
			@include col-sm-12();
			margin-bottom: -116px;
			position: relative;
			z-index: 1;

			.contacs-row{
				display: -webkit-flex;
				display: -moz-flex;
				display: -ms-flex;
				display: -o-flex;
				display: flex;
				align-items: flex-start;
				margin-bottom: 30px;

				&:last-child{
					margin-bottom: 0;
				}
				
			}
			.contacs-row__key{
				width: 176px;
				flex-shrink: 0;
				font-family: $m;
			}
			.contacs-row__val{
				margin-left: 10px;
				font-family: $l;

				a{

					color: #000;
					text-decoration: none;
					&:hover, &:active{
						text-decoration: underline;
					}
					
				}
			}
			.contacts-socials{
				display: inline-block;
				margin-left: 15px;
				vertical-align: middle;
			}
		}

	}

	&--card-page{
		.red-decor{
			height: calc(100% + 45px);
			width: calc(50% + 380px);
			bottom: auto;
			top: -45px;
			background-color: #fafafa;
		}

	}
		

		.contacts-map{
			height: 794px;

			.ipc-map-marker{
				display: -webkit-flex;
				display: -moz-flex;
				display: -ms-flex;
				display: -o-flex;
				display: flex;
				align-items: flex-start;
				padding: 5px 10px 0 0;
				background: #FFFFFF;

				line-height: 17px;
				font-size: 14px;

				img{
					flex-shrink: 0;
					margin-right: rem(20px);
				}

				strong{
					display: block;
				}

		}

	}
}


@media only screen and (max-width : 1200px){
	.contacts{
		padding-top: rem(50px);
		background-color: #FAFAFA;
	}
	.contacts .wrapper{
		position: static;

	}
	.contacts .wrapper .contacts-info{
		// margin-bottom: 0;
		padding: 30px;
		padding-bottom: rem(50px);
		background-color: #EEEEEE;
	}

	.contacts .wrapper h2{
		margin-top: 0;
	}
}
@media only screen and (max-width : 1100px){
	// .contacts .wrapper{
	// 	justify-content: center;
	// }
}
@media only screen and (max-width : 960px){
	.contacts .contacts-text .contacts-info{
		font-size: rem(16px);
		margin-bottom: -73px;
	}
	.contacts .contacts-text .contacts-info .contacs-row{
		margin-bottom: 15px;
	}
	.contacts .contacts-text .contacts-info .ipc-soc-icon .icon{
		font-size: 24px;
	}


}
@media only screen and (max-width : 780px){
	.contacts .contacts-text .contacts-info .contacs-column:first-child .contacs-row__key,
	.contacts .contacts-text .contacts-info .contacs-row__key{
		width: 50%;
	}
	.contacts .contacts-text .contacts-info{
		display: block;
		padding-left: 30px;
		padding-right: 30px;
	}
	.contacts .contacts-text .contacts-info{
		font-size: 18px;
		margin-bottom: -73px;
	}
	.contacts .contacts-text .contacts-info .contacs-column:first-child .contacs-row:last-child{
		margin-bottom: 15px;
	}
	.contacts .wrapper .contacts-info .contacts-socials{

	}
	.contacts .wrapper .contacts-info{
		margin-bottom: 0;
	}
}

@media only screen and (max-width : 560px){
	.contacts .wrapper .contacts-info{
		padding-left: 15px;
		padding-right: 15px;
		font-size: 14px;
		margin-bottom: 30px;
	}
	.contacts .wrapper .contacts-info .contacts-socials{
		margin-left: 0;
		margin-top: 10px;
	}
	.contacts .wrapper .contacts-info .contacs-column:first-child .contacs-row__key,
	.contacts .wrapper .contacts-info .contacs-row__key{
		width: 40%;
	}
}
@media only screen and (max-width : 480px){
	.contacts .wrapper .contacts-info{
		margin-left: 0;
		margin-right: 0;
	}
}
@media only screen and (max-width : 375px){
	.contacts .wrapper .contacts-info{
		font-size: 13px;
	}
}
/*--end contacts--*/