/*--main-banner--*/
.main-banner{
	position: relative;
	
	background-color: #F2F2F2;
	.red-decor{
		width: calc((100% - 1200px)/2 + 250px);
		height: calc(100% + 92px);

		&:before{
			content: '';
			width: 1000px;
			left: -255px;
		}

	}
	.wrapper{
		position: relative;
		padding-top: rem(76px);
		padding-bottom: rem(94px);
		justify-content: space-between;
		position: relative;
		z-index: 1;
		.main-banner__img{
			position: absolute;
			height: 100%;
			width: 100%;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
	
			img{
				position: absolute;
				top: -15px;
				margin-left: 80px;
				left: 50%;
				transform: translateX(-50%);
			}

		}
		.main-banner__left{
			position: relative;
			@include col();
			@include col-7();
			@include col-md-6();
			@include col-sm-12();
			z-index: 1;

			h1{
				margin-bottom: rem(36px);
			}

			.delf-subtitle{
				margin-bottom: rem(34px);
			}
			ul{
				margin-bottom: rem(99px);
				li{
					margin-bottom: rem(15px);
					font-family: $m;

					list-style-type: none;
				}
			}

			// .button{
			// 	width: 362px;
			// }
		}
		.main-banner__right{
			position: relative;
			@include col();
			@include col-4();
			@include col-lg-5();
			@include col-md-6();
			@include col-sm-12();
			padding-left: 68px;
			padding-top: rem(127px);
			z-index: 2;

			.main-banner__feature{
				position: relative;
				display: -webkit-flex;
				display: -moz-flex;
				display: -ms-flex;
				display: -o-flex;
				display: flex;
				align-items: flex-start;
				margin-bottom: rem(111px);
				text-decoration: none;

				&:last-child{
					margin-bottom: 0;
				}
			}
			.main-banner__icon{
				width: 45px;
				height: 45px;
				flex-shrink: 0;
				margin-right: rem(30px);
				// background: 0 0 no-repeat;
				// background-size: 100%;

				&--red{
					display: none !important;
				}
			}
			.main-banner__feature-text{
				color: #fff;
				font-size: rem(24px);
				font-family: $m;
			}

			.main-banner__feature-arrow{

				display: block;
				width: 1em;
				height: 1em;
				position: absolute;
				top: 50%;
				right: 20px;
				transform: translate(0,-50%) rotate(45deg);
				border-right: 2px solid $font;
				border-top: 2px solid $font;
				opacity: .5;
				transition: .3s;
				display: none;
			}
		}
	}
}



@media only screen and (max-width : 1200px){
	.main-banner .wrapper .main-banner__right .main-banner__feature-text{
		color: $accent;
	}
	.main-banner .wrapper .main-banner__right .main-banner__icon--wht{
		display: none !important;
	}
	.main-banner .wrapper .main-banner__right .main-banner__icon--red{
		display: block !important;
	}
	.main-banner .wrapper .main-banner__img img{
		max-width: 60%;
		left: 15%;
		transform: none;
	}

}
@media only screen and (max-width : 1100px){
	.main-banner .wrapper .main-banner__right{
		padding-left: 0;
	}
}
@media only screen and (max-width : 1100px){
	.main-banner{
		overflow: hidden;
	}
	.main-banner .wrapper .main-banner__img img{
		max-width: 200%;
		left: 50%;
		right: auto;
		transform: none;
	}
}
@media only screen and (max-width : 780px){
	.main-banner{
		padding-top: 45px;
	}
	.main-banner .wrapper .main-banner__img img{
		max-width: 200%;
		left: 30%;
		right: auto;
		transform: none;
	}
	.main-banner .wrapper .main-banner__right{
		padding: 30px;
		margin-top: 30px;
		width: 100%;
		margin-left: 0;
		margin-right: 0;
		background-color: #fff;

	}
	.main-banner .wrapper .main-banner__right .main-banner__feature{
		margin-bottom: 30px;

	}
	.main-banner .wrapper .main-banner__right .main-banner__feature-arrow{
		display: block;
	}
	.main-banner .wrapper .main-banner__left ul{
		margin-bottom: 30px;
	}
}
@media only screen and (max-width : 560px){
	.main-banner .wrapper .main-banner__img img{
		left: -10%;
	}
	.main-banner .wrapper .main-banner__left ul li{
		font-size: 14px;
	}
	.main-banner .wrapper .main-banner__left .delf-subtitle{
		font-size: 18px;
	}
}
@media only screen and (max-width : 480px){
	.main-banner__feature-price{
		display: none;
	}
	.main-banner .wrapper .main-banner__right .main-banner__feature{
		align-items: center;
		padding-right: 40px;
	}
	.main-banner .wrapper .main-banner__right .main-banner__feature-arrow{
		right: 0;
	}

}
/*--end main-banner--*/