/*--why--*/
.why{
	padding-top: rem(177px);
	padding-bottom: rem(220px);
	
	.wrapper{
		h2{
			margin-bottom: rem(118px);
		}
		.title-wrapper{
			position: relative;
			z-index: 1;
		}
	}

	.why-content{
		align-items: flex-start;
		.why-left,
		.why-right,
		.why-img{
			@include col();
			@include col-4();
			@include col-sm-12();
		}
		.why-left,
		.why-right{
			position: relative;
			z-index: 1;
		}
		.why-right{
			padding-left: rem(68px);
			
			.why-item{

 				&:first-of-type{
					margin-bottom: 95px;
 				}
 				&:nth-child(2){
					margin-bottom: 95px;
 				}
			}

		}
		.why-left{
			.why-item{

 				&:first-of-type{
					margin-bottom: 72px;
 				}
 				&:nth-child(2){
					margin-bottom: 95px;
 				}
			}
		}

		.why-img{
			position: relative;

			.why-visual{
				position: absolute;
				left: -90%;
				top: -115px;
			}
			.why-pointer{
				position: absolute;
				left: -45%;
				top: -70px;
			}
		}

		.why-item{
			// margin-bottom: rem(103px);
			// &:last-child{
			// 	margin-bottom: 0;
			// }
		}
		.why-item__title{
			font-size: rem(24px);
			font-family: $m;
			color: $accent;
		}
		.why-item__text{
			margin: 0;
		}
		.why-note{
			margin-top: rem(89px);
			font-size: 14px;
		}
	}
}

@media only screen and (max-width : 1200px){
	.why{

		padding-bottom: rem(100px);
	}
	.why .why-content{
		align-items: stretch;
	}
 .why .why-content .why-img .why-visual{
 	max-width: 200%;
 	left: -50%;
 }
 .why .why-content .why-img .why-pointer{
 	max-width: 200%;
 	top: -18%;
 	left: -50%;
 }
}

@media only screen and (max-width : 1100px){
	.why{
		padding-top: rem(94px);
	}
	 .why .why-content .why-img .why-pointer{
 		display: none !important;
 
	}
	.why .why-content .why-img .why-visual{
		top: 0;
	}

}
@media only screen and (max-width : 980px){
	.why .why-content .why-right{
		padding-left: 0;
	}
}
@media only screen and (max-width : 780px){
	.why{
		.wrapper{
			h2{
				margin-bottom: rem(40px);
			}

		}
	}
	.why .why-content .why-left{
		order: 2;
	}
	.why .why-content .why-left .why-item,
	.why .why-content .why-right .why-item{
		br{
			display: none;
		}
	}
	.why .why-content .why-left .why-item,
	.why .why-content .why-right .why-item,
	.why .why-content .why-left .why-item:first-of-type,
	.why .why-content .why-left .why-item:nth-child(2),
	.why .why-content .why-right .why-item:first-of-type,
	.why .why-content .why-right .why-item:nth-child(2){
		margin-bottom: 30px;
	}
	.why .why-content .why-note{
		margin-top: 0;
	}

	.why .why-content .why-img{
		display: none;
	}
	.why .why-content .why-item__title{
		font-size: rem(30px);
	}
	.why .why-content .why-item__text{
		font-size: rem(24px);
	}
}
@media only screen and (max-width : 480px){
	.why .why-content .why-item__title{
		font-size: 20px;
	}
	.why .why-content .why-item__text{
		font-size: 16px;
	}
}


/*--end why--*/