/*--managers--*/
.managers{
	padding-top: rem(60px);
	padding-bottom: rem(170px);

	.wrapper{
		h2{
			margin-bottom: rem(60px);
		}
	}

	
	&-top{
		display: block;		

				.managers-slider-wrapper{
					padding-top: 30px;
					margin-top: -30px;
					width: calc(100% + 60px);
					margin-left: -30px;
					overflow: hidden;
				}

			.managers-slider{
				margin-bottom: 40px;
				.slick-slide{
					margin: 0 4px;
					transform: scale(0);

					&.slick-active{
						transform:scale(1);
					}
				}
				.slick-list{
				// padding-top: 5px;
				// padding-bottom: 30px;
				padding: 0 15px;
				margin-right: 15px;
				overflow: visible;
			}

			.managers-slide{
				padding: 25px 0 30px 0;
				background-color: #fff;
				box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
				text-align: center;		
				border: 2px solid transparent;
				transition: all .3s;
				color: #000;

				&:hover, &:active{
					transform: translateY(-30px);
					border-color: $accent;
					box-shadow: 0px 20px 34px rgba(0, 0, 0, 0.25);
				}

				.managers-slide__img{
					position: relative;
					padding: 0 25px;

					height: 260px;
					margin-bottom: 19px;

					img{
						display: block;
						max-width: 100%;
						max-height: 100%;
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%,-50%);
					}
				}

				.managers-slide__text{
					padding: 0 10px;
				}
				
				.managers-slide__name{
					margin-bottom: 7px;
					font-size: rem(24px);
				}
				&__inner{

				}
			}

		}
	}
	

	.managers-bottom{
		justify-content: space-between;
		align-items: center;

		.button{
			width: 421px;
			padding-left: 15px;
			padding-right: 15px;
		}
	}
}

@media only screen and (max-width : 1200px){
	.managers-top .managers-slider-wrapper{
		width: 100%;
		margin-left: 0;
	}
	.managers{
		padding-bottom: rem(94px);
	}
}


@media only screen and (max-width : 960px){
	.managers-top .managers-slider .managers-slide .managers-slide__name{
		font-size: 18px;
	}
	.managers-top .managers-slider .managers-slide .managers-slide__direction{
		font-size: 14px;
	}
}
@media only screen and (max-width : 780px){
	.managers{
		display: none;
	}
}
/*--end managers--*/