/*--main styles--*/
*{
	box-sizing: border-box;
}
html{
	font-size: 18px;

	&.freezed{
		overflow: hidden;
    width: 100%;
    // height: 100vh;

	    body{
	    	    overflow-y: hidden;
		    // position: fixed;
		    width: 100%;
		    // height: 100vh;
	    }
	}
}
html, body{
	margin: 0;
	padding: 0;
}
body{
	font-family: $r;
	font-weight: normal;
	min-width: 320px;
	font-size: 18px;
	position: relative;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
	color: $font;
}
input{
	outline: none;
}
.wrapper{
	@include row-flex();
	@include wrapper();
}
.title-wrapper{
	@include col();
	@include col-12();
}
.dark{
	background-color: $dark;

	h2{
		color: #fff;
	}
	.delf-subtitle{
		color: $accent;
	}

}
.delf-gray{
	background: #FAFAFA;
}
h1, h2{
	margin: 0;
	font-size: rem(36px);
	font-family: $l;
	font-weight: normal;
	text-transform: uppercase;
	text-align: center;

	strong{
		font-family: $b;
	}
}
.delf-subtitle{
	font-size: rem(24px);
	font-family: $m;
	line-height: rem(28px);

	strong{
		font-family: $b;
	}
}
.delf-form{
	.delf-form-label{
		display: block;

		.delf-form-input{
			display: block;
			width: 100%;
			padding: 18px;
			border: none;
			line-height: 18px;
			background: #FFFFFF;
			box-shadow: inset 0px 1px 2px rgba(0, 0, 0, 0.25);
			font-size: 18px;
			font-family: $l;
			@include placecolor(rgba(0, 0, 0, 0.25));

		}
		.delf-label-name{
			display: block;
			margin-left: 26px;
			margin-bottom: 20px;
			font-family: $l;
			// font-size: 14px;
		}
	}
	.delf-form__bottom{
		text-align: center;
	}
	.delf-form-button{
		cursor: pointer;
	}
	.delf-custom-check{
		position: relative;
		font-family: $l;

		a{
			color: $font;
			text-decoration: none;
			&:hover, &:active{
				text-decoration: underline;
			}
			
		}

		.delf-form-checkbox{
			position: absolute;
		    box-sizing: border-box;
		    width: 1px;
		    height: 1px;
		    display: inline-block;
		    left: 14px;
		    top: 100%;
		    margin: 0;
		    padding: 0;
		    opacity: 0;

			&:checked ~ .delf-custom-box{
				.icon{
					display: block;
					color: $accent;
					
				}
			}
		}
		.delf-custom-box{
			position: relative;
			display: inline-block;
			vertical-align: middle;
			margin-right: rem(28px);
			width: 29px;
			height: 29px;
			background-color: #fff;

			.icon{
				@include absolute-center;
				color: #000;
				font-size: 12px;
				display: none;
			}
		}
		.delf-form-consent{
			font-size: 14px;
		}
	}

}
.red-decor{
	position: absolute;
	right: 0;
	top: 0;
	height: 100%;
	background-color: $accent;

	&:before{
		content: '';
		display: block;
		position: absolute;
		height: 100%;
		top: 0;
		background-color: $accent;
		transform: skewX(-30deg);

	}

}
.delf-marked{
	position: relative;
	padding-left: rem(40px);

	&:before{
		content: '';
		position: absolute;
		top: rem(2px);
		left: 0;
		display: block;
		width: rem(24px);
		height: rem(13px);
		background:  url(../img/icons-svg/list-mark.svg) 0 0 no-repeat;
		background-size: 100%;
	}

}
.button-wrapper{
	@include col();
	@include col-12();
	text-align: center;
}
.button{
  display: inline-block;
  width: 362px;
  border: 1px solid $accent;
  color: #fff;
  text-decoration: none;
	font-family: $b;
  padding: 23px;
  font-size: rem(18px);
  text-transform: uppercase;
  background-color: $accent;
  box-shadow: 0px 10px 20px rgba(239, 60, 73, 0.2);
  text-align: center;
  position: relative;
  outline: none;
  transition: all .1s ease;
	
	  &:hover{
	     background: #FF5965;
	  }
	   
	  &:active{
	   background-color: transparent;
	     color: #000;
	  }

  &-mini{
  	padding: 14px;
  	font-size: 12px;
  }
  &-empty{
  	background-color: transparent;
  	color: $font;

  	&:hover{
		color: $accent;
		background-color: transparent;
  	}
  	&:active{
  		border-color: transparent;
  		background-color: #2A3D47;
  		color: #fff;

  	}
  	

  }

}
.toggle-mnu{

  display: none;
  width: 28px;
  height: 28px;
  // margin-top: 14px;
  position: absolute;
  right: 15px;
  top: 50%;
  margin-top: -14px;

  span:after, span:before{

    content: "";
    position: absolute;
    left: 0;
    top: 9px;
  } 

  span:after{

    top: 18px;
  }

  span{

    position: relative;
    display: block;
  } 

  span, span:after, span:before{

    width: 100%;
    height: 2px;
    background-color: $font;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px;
  }


  &.on span{

    background-color: transparent;
  } 

  &.on span:before{

    transform: rotate(45deg) translate(-1px, 0px);
  } 

  &.on span:after{

    transform: rotate(-45deg) translate(6px, -7px);
  } 
}
.ruble{
	font-family: $rub-verdana;
}
ul, li{
	margin: 0;
	padding: 0;
}
.delf-text{
	&-center{
		text-align: center;
	}
	&-right{
		text-align: right;
	}
	&-left{
		text-align: left;
	}
}
.delf-card{
	display: block;
	padding: 24px 29px 22px 29px;
	border: 1px solid #DADADA;
	background-color: #fff;
	color: $font;
	text-decoration: none;

	.button{
		width: 100%;
	}
	.delf-card__title{
		margin-bottom: 8px;
		font-family: $m;
		text-align: center;
	}
	.delf-card__img{
		height: 184px;
		margin-bottom: 17px;
		position: relative;

		img{
			display: block;
			max-width: 100%;
			max-height: 100%;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%)
		}
	}
	.delf-card__price{
		margin-bottom: 26px;
		font-size: 24px;
		font-family: $b;
		text-align: center;
		color: $accent;
	}
	.delf-card__info{
		font-size: 14px;
		text-align: center;
	}
	.delf-card__row{
		margin-bottom: 3px;
	}
	.delf-card__key,
	.delf-card__val{
		display: inline-block;
		vertical-align: top;
	}
	.delf-card__purpose{
		margin-bottom: 24px;
		font-family: $b;
	}

	&:hover{
		position: relative;
		z-index: 1;
		box-shadow: 0px 20px 32px rgba(92, 93, 94, 0.3);
		border-color: $accent;
		.button{
			background-color: #FF5965;
		}
			
	}
	&:active{
		box-shadow: none;
		border-color: transparent;
		.button{
			background-color: transparent;
			color: #000;
			
		}
	}
	&:focus{
		outline: none;
	}
	
}
.delf-card__info{
	width: 100%;
	margin-bottom: 10px;
	border-collapse:collapse;
	font-size: 14px;
	line-height: 18px;

	tr{
		vertical-align: top;
		td{
			padding-bottom: 10px;
			&:last-child{
				padding-left: 5px;
				font-family: $m;
			}
			&:first-child{
				font-family: $l;
				width: 140px;
			}
		}
	}
}
.slider-handlers__prev,
.slider-handlers__next{
	display: inline-block;
	vertical-align: middle;
	cursor: pointer;
	user-select: none;
	.icon{
		color: #DADADA;
		font-size: 38px;
	}

	&:hover, &:active{
		.icon{
			color: $accent;
		}
	}
	
}
.delf-soc-icon{
	margin-left: 20px;
	text-decoration: none;
	display: inline-block;
	vertical-align: middle;
	.icon{
		font-size: 30px;
		color: $accent;
	}
	&:first-child{
		margin-left: 0;
	}

	&:hover{
		.icon{
			color: #FF5965;
		}
	}
	&:active{
		.icon{
			color: #fff;
		}
	}
}
.delf-padded-left{
	padding-left: rem(68px);
}
.delf-list-marked-dash{
	li{
		line-height: 28px;

		&:before{
			    display: inline-block;
    vertical-align: middle;
    content: '';
    background-color: $accent;
    height: 1px;
    width: 1.5em;
    margin: 0 .3em 0 0;
		}

	}
}
.delf-link-red{
	font-family: $m;
	color: $accent;
}
.hidden{
	display: none;
}
/*--end main styles--*/
